<template lang="pug">
doctype html

#app


  b-navbar(toggleable="md" fixed="top" type="dark" variant="dark")

    b-nav-toggle(target="nav_collapse")

    b-navbar-brand(to="/")
      img.mr-2(src="./assets/logo.png" height="28")
      | MODBUS TESTER
          

  b-container
    b-alert(variant="danger" :show="error!=null" dismissible) {{error}}
    b-row
      b-col
      b-col(cols=6)
        b-form-group(label="IP PORT")
            b-input-group
                b-form-input(v-model.trim="config.ip" )
                b-form-input(v-model.trim="config.port")
        b-form-group(label="REGISTERS")
            b-input-group
              b-form-input(v-model.trim="config.startReg" placeholder="Start register")
              b-form-input(v-model.trim="config.lastReg")
        b-form-group(label="Type")
          b-form-select(v-model="config.type")
            option(value="1") TCP
            option(value="2") RTU over TCP/IP
        b-form-group(label="RTU Settings" v-if="config.type == 2")
          b-input-group
              //b-form-input(v-model="config.baudRate" placeholder="BaudRate")
              //b-form-input(v-model="config.parity" placeholder="Parity")
              b-form-input(v-model="config.slaveId" placeholder="Slave ID")
        
        b-button(@click="send" :variant="isConnected?'':'primary'") {{isConnected?'Stop':'Start'}}
        b-row.mt-4
          b-col
            h4 Console
            code.text-center(:class="{flash}") {{json['0']}}
      b-col

  .footer 
    .container.text-muted &copy; 2019 BeInTech LLC. All rights reserved. Abzal Aiten, Timur Junussov, Mars Akishev
    

</template>

<script>

import axios from 'axios'
import Vue from 'vue'

var socket;
var http = axios.create({
  baseURL:'http://localhost:3000'
})

export default {
  name: 'app',
  data(){
    return {
      ws:{
        isOpen : false,
        start: false,
        error: null,
        message: null,
        server: localStorage.getItem('ws')||(location.protocol=='https:'?'wss://':'ws://')+location.host
        //server: 'ws://localhost:3000'
      },
      json:{'0':[],isOpen:false},
      isConnected:false,
      error:null,
      flash:false,
      config:{
        ip:"87.255.213.153",
        port:10052,
        lastReg:5,
        startReg:0,
        type:1,
        parity:0,
        baudRate:9600,
        slaveId:1,
      }
    }
  },
  created(){
    this.connect();
    if(localStorage.getItem("config") !== null) this.config = JSON.parse(localStorage.getItem("config"));
  },
  beforeDestroy(){
    socket.close()
  },
  methods:{
    logout(){
    },
    send(){
      this.error = null;
      
      localStorage.setItem("config",JSON.stringify(this.config))
      axios.post('/toggle',this.config).then((resp)=>{
      //http.post('/toggle',this.config).then((resp)=>{
        console.log(resp.data)
        this.isConnected = resp.data.isOpen;
        this.error = resp.data.error
        this.json = {'0':[],isOpen:false};
      });
    },
    connect(){
      socket = null;
      socket = new WebSocket(this.ws.server);
      socket.addEventListener('open', (e) => {
        this.ws.isOpen = true
        socket.send('/');
      });
      socket.addEventListener('error', (e) => {
        console.error('error',e)
        this.ws.error = e
        this.ws.isOpen = false
      });
      socket.addEventListener('close', (e) => {
        // console.log('close',e)
        this.ws.isOpen = false
      });
      socket.addEventListener('message', (msg) => {
        console.log('messsage',msg.data)
        //this.slaves = JSON.parse(msg.data)

        this.json = JSON.parse(msg.data);
        this.isConnected = this.json.isOpen;
        this.flash = true;

        setTimeout(()=>{
          this.flash = false;
        },500)
        
      });
    }
  }
}
</script>

<style lang="stylus">

#app
  font-family 'Avenir', Helvetica, Arial, sans-serif
  -webkit-font-smoothing antialiased
  -moz-osx-font-smoothing grayscale
  color #2c3e50
  margin-top 60px

body
  padding-top 3.5rem
  
.flash 
  background rgba(255,0,0,0.3);
  transition all .3s ease

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px; /* Set the fixed height of the footer here */
  line-height: 60px; /* Vertically center the text there */
  background-color: #f5f5f5;
  text-align: center;
}
  


// .reg
//   display inline-block
//   border 1px solid #ccc
//   padding 5px
//   margin-right 2px
//   font-size 14px !important
//   width 90px
  

</style>



